.budget-wrapper {
    width: 100%;
    .budget-input-wrapper {
        input {
            width: 100%;
        }
        .budget-dropdown {
            max-height: 200px;
            overflow: scroll;
            cursor: pointer;
            .dropdown-item {
                padding: 4px 0;
                display: flex;
                justify-content: space-between;
                .highlight {
                    background-color: $primary;
                    color: #ffffff;
                    font-weight: bold;
                    padding: 2px;
                    border-radius: 4px;
                }
                &.selected {
                    background-color: $info;
                    color: #ffffff;
                }
                .fixed-label {
                    background: $primary;
                }
                .flex-label {
                    background: $warning;
                }
//                &.fixed-budget {
//                    div {
////                        background: $primary;
//                        &:after {
//                            content: ' fixed';
//                            font-size: 6px;
//                        }
//                    }
//                }
//                &.fixed-budget {
//                    div {
////                        background: $warning;
//                        &:after {
//                            content: ' flex';
//                            font-size: 6px;
//                        }
//                    }
//                }
            }

        }
    }
    .budget-display {
        height: 34px;
        .removable-budget {
            cursor: pointer;
            transition: all, .3s, ease;
            //position: relative;
            &:hover {
                padding: .4em 1.6em .4em;
                .fa-times {
                    display: inline;
                }
            }
            .fa-times {
                font-size: 10px;
                //position: relative;
                //left: 8px;
                display: none;
            }
        }
    }
}